<template>
  <div v-loading="loading">
    <el-card class="box-card">
      <div class="space"></div>
      <!--  -->
      <el-form :inline="true" :model="search">
        <el-form-item label="" :size="'small'" prop="shopGearList">
          <el-select size="small"  v-model="search.shopGear" placeholder="">
            <el-option value="" label=""></el-option>
            <el-option value="1" label="1">1</el-option>
            <el-option value="2" label="2">2</el-option>
            <el-option value="3" label="3">3</el-option>
            <el-option value="4" label="4">4</el-option>
            <el-option value="5" label="5">5</el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" :size="'small'" prop="shopLevelList">
          <el-select size="small"  v-model="search.shopLevel" placeholder="">
            <el-option value="" label=""></el-option>
            <el-option value="1" label="1">1</el-option>
            <el-option value="2" label="2">2</el-option>
            <el-option value="3" label="3">3</el-option>
            <el-option value="4" label="4">4</el-option>
            <el-option value="5" label="5">5</el-option>
            <el-option value="6" label="6">6</el-option>
            <el-option value="7" label="7">7</el-option>
            <el-option value="8" label="8">8</el-option>
          </el-select>
        </el-form-item>
        <el-form-item :size="'small'" label="">
          <el-input placeholder="" v-model="search.keyword" class="input-with-select" style="width: 300px">
            <el-select v-model="search.keywordType" slot="prepend" @change="inputWithSelectChange" placeholder="" style="width: 120px">
              <el-option label="" value="1"></el-option>
              <el-option label="" value="2"></el-option>
              <el-option label="" value="3"></el-option>
              <el-option label="" value="4"></el-option>
              <el-option label="" value="5"></el-option>
            </el-select>
          </el-input>
        </el-form-item>
        <el-form-item label="" prop="saleZoneCode">
          <el-select size="small" v-model="search.saleZoneCode" placeholder="">
            <el-option
                v-for="(item,index) in saleZone"
                :key="index"
                :label="item.zoneName"
                :value="item.zoneCode">
            </el-option>
          </el-select>
        </el-form-item>
        <div></div>
        <el-form-item>
          <el-button size="small" type="primary" @click="list"></el-button>
          <el-button size="small" @click="reset"></el-button>
        </el-form-item>
      </el-form>
    </el-card>
    <div class="space"></div>
    <el-card>
      <!--  -->
      <el-table :data="tableList" style="width: 100%">
        <el-table-column label="" type="index" align="center" width="50">
          <template slot-scope="scope">
            {{ (search.page-1)*search.pageSize + scope.$index + 1 }}
          </template>
        </el-table-column>
        <el-table-column prop="shopGear" label="" align="center"></el-table-column>
        <el-table-column prop="shopLevel" label="" align="center"></el-table-column>
        <el-table-column prop="licenseNo" label="" align="center"></el-table-column>
        <el-table-column prop="shopName" label="" width="150" align="center"></el-table-column>
        <el-table-column prop="contactName" label=""  align="center"></el-table-column>
        <el-table-column prop="contactPhone" label="" align="center"></el-table-column>
        <el-table-column
            prop="detailAddr"
            label=""
            width="120">
          <template slot-scope="scope">
            {{ scope.row.provName + scope.row.cityName + scope.row.countyName + scope.row.detailAddr }}
          </template>
        </el-table-column>
        <el-table-column property="saleZoneCode" label="" align="center" width="180px">
          <template slot-scope="scope">
            {{ getSaleZoneName(scope.row.saleZoneCode) }}
          </template>
        </el-table-column>


      </el-table>
      <div class="space"></div>
      <!--  -->
      <el-pagination :small="false" background layout="total, prev, pager, next, jumper"
                     @size-change="sizeChange" @current-change="currentChange"
                     :current-page="search.page" :page-size="search.pageSize"
                     :total="pagination.total">
      </el-pagination>
    </el-card>

  </div>
</template>

<script>
export default {
  data() {
    return {
      saleZoneCode: sessionStorage.getItem('isAllSaleZone') == 1 ? null : sessionStorage.getItem('saleZoneCode'),
      search: {
        page: 1,
        pageSize: 10,
        shopLevel:null,
        shopGear:null,
        keywordType:null,
        keyword:"",
        saleZoneCode: sessionStorage.getItem('isAllSaleZone') == 1 ? null : sessionStorage.getItem('saleZoneCode'),
      },
      pagination: { // 
        total: 0
      },
      tableList: [],
      saleZone: [],

      form: {
        show: false, // 
        id: '',
        shopImg: '',
        licenseImg: '',
        auditStatus: ''
      },
      loading:false
    }
  },
  created() {
    this.getSaleZone();
    this.list();
  },
  methods: {
    getSaleZone() {
      this.$request.post('/api/saleZone/userSzList', {}, true, (res) => {
        if (res.code == '200') {
          this.saleZone = res.data || []
          if (this.isAllSaleZone == 1) {
          } else {
            this.search.saleZoneCode = this.saleZoneCode
          }
        }
      })
    },
    getSaleZoneName(saleZoneCode){
      if(!saleZoneCode) return ""
      let zoneName = ""
      let item = this.saleZone.find((i) => {
        if(i.zoneCode == saleZoneCode){
          return i
        }
      })
      // console.log(item)
      zoneName = item ? item.zoneName : ''
      return zoneName
    },
    inputWithSelectChange(val){
      this.search.keyword = ""
    },
    currentChange(page) {
      // pageNo
      this.list(event, page);
    },
    sizeChange(pageSize) {
      // pageNo
      this.list(event, null, pageSize);
    },
    // page = {"pageCount":, "count":}
    initPagination(total) {
      if(total) {
        this.pagination.total = total;
      }
    },
    // 
    reset() {
      this.search = {
        page: 1,
        pageSize: 10,
        shopLevel:null,
        shopGear:null,
        keywordType:null,
        keyword:"",
        saleZoneCode:null
      }
      this.list();
    },
    // 
    list(_event, page, pageSize) {
      let _pageNo = 1;
      if(page) _pageNo = page;
      this.search.page = _pageNo;
      let _pageSize = 10;
      if(pageSize) _pageSize = pageSize;
      this.search.pageSize = _pageSize;
      this.$request.post('/saasHbseller/groupManager/query', this.search, true, (res)=>{
        if (res.code == '200') {
          this.tableList = res.data.infoList.records || [];
          this.initPagination(res.data.infoList.total);
        }else {
          this.$message.error(res.msg);
        }
      });
    },
  }
}
</script>

<style scoped>
.space {position:relative;width:100%;height:20px;}
.el-input, .el-select {
  width: 200px;
}
</style>



