<template>
  <div>
    <div v-show="business==1">
      <el-card>
        <el-row>
          <el-button size="small" type="primary" @click="business=2"></el-button>
        </el-row>
        <div class="space"></div>
        <!--  -->
        <el-form :inline="true" :model="search" class="search-block">
          <el-form-item label="">
            <el-input size="small" v-model="search.name" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-select size="small" v-model="search.htype" placeholder="">
              <el-option label="" value=""></el-option>
              <el-option label="" value="1"></el-option>
              <el-option label="" value="2"></el-option>
            </el-select>
          </el-form-item>

          <div></div>
          <el-form-item>
            <el-button size="small" type="primary" @click="list"></el-button>
            <el-button size="small" @click="resetSearch"></el-button>
          </el-form-item>
        </el-form>
      </el-card>
      <div class="space"></div>
      <el-card>
        <!--  -->
        <el-table :data="tableList" style="width: 100%">
          <el-table-column label="" type="index" align="center" width="80px">
            <template slot-scope="scope">
              {{ (search.pageNo - 1) * search.pageSize + scope.$index + 1 }}
            </template>
          </el-table-column>
          <el-table-column prop="actCode" label="" align="center"></el-table-column>
          <el-table-column prop="actName" label="" align="center"></el-table-column>
          <el-table-column prop="htype" label="" align="center">
            <template slot-scope="scope">
              <span v-if="scope.row.htype==1"></span>
              <span v-if="scope.row.htype==2"></span>
            </template>
          </el-table-column>
          <el-table-column prop="status" label="" align="center">
            <template slot-scope="scope">
              <span v-if="scope.row.status==1"></span>
              <span v-if="scope.row.status==2"></span>
            </template>
          </el-table-column>
          <el-table-column label="" align="center" width="300px">
            <template slot-scope="scope">
              {{scope.row.hstimeStr}} - {{scope.row.hetimeStr}}
            </template>
          </el-table-column>
          <el-table-column label="" align="center" width="300px">
            <template slot-scope="scope">
              {{scope.row.actStimeStr}} - {{scope.row.actEtimeStr}}
            </template>
          </el-table-column>
          <el-table-column label="" align="center" width="200">
            <template slot-scope="scope">
              <el-button size="mini" @click="edit(scope.$index, scope.row)"></el-button>
              <el-button size="mini" v-if="scope.row.status==1" @click="mstatus(scope.row.id, 2)"></el-button>
              <el-button size="mini" v-if="scope.row.status==2" @click="mstatus(scope.row.id, 1)"></el-button>
            </template>
          </el-table-column>
        </el-table>
        <div class="space"></div>
        <!--  -->
        <el-pagination background layout="total, prev, pager, next, jumper"
          @size-change="sizeChange" @current-change="currentChange"
          :current-page="form.pageNo" :page-size="form.pageSize"
          :total="pagination.total">
        </el-pagination>
      </el-card>
    </div>
    <div v-show="business==2">
      <el-card class="box-card">
        <el-form :model="form" :rules="rules" ref="form" label-width="150px">
          <el-form-item label="" prop="actName">
            <el-input size="small" v-model="form.actName"></el-input>
          </el-form-item>
          <el-form-item label="" prop="hstimeStr">
            <el-date-picker v-model="form.hstimeStr" type="datetime" value-format="yyyy-MM-dd HH:mm:ss" placeholder=""></el-date-picker>
            
            <el-date-picker v-model="form.hetimeStr" type="datetime" value-format="yyyy-MM-dd HH:mm:ss" placeholder=""></el-date-picker>
          </el-form-item>
          <el-form-item label="" prop="actStimeStr">
            <el-date-picker v-model="form.actStimeStr" type="datetime" value-format="yyyy-MM-dd HH:mm:ss" placeholder=""></el-date-picker>
            
            <el-date-picker v-model="form.actEtimeStr" type="datetime" value-format="yyyy-MM-dd HH:mm:ss" placeholder=""></el-date-picker>
          </el-form-item>
          <el-form-item label="" prop="note">
            <el-input size="small" type="textarea" v-model="form.note" :autosize="{ minRows: 3, maxRows: 10}"></el-input>
          </el-form-item>
          <el-form-item label="" prop="htype" required>
            <el-radio size="small" v-model="form.htype" :label="1"></el-radio>
            <el-radio size="small" v-model="form.htype" :label="2"></el-radio>
            <p class="form-note">
              <br>
              <span class="pl36"></span>
            </p>
          </el-form-item>
          <el-form-item label="" prop="status" required>
            <el-radio size="small" v-model="form.status" :label="1"></el-radio>
            <el-radio size="small" v-model="form.status" :label="2"></el-radio>
          </el-form-item>
          <el-form-item label="" required>
            <table class="award-table" border="1">
              <thead>
                <td></td>
                <td></td>
                <td></td>
              </thead>
              <tr v-for="(item, index) in form.exchanges" :key="item.key">
                <td>
                  <input type="hidden" v-model="item.id"/>
                  <input type="number" v-model="item.srange"/>
                </td>
                <td>
                  <input type="number" v-model="item.num"/>
                </td>
                <td>
                  <el-button size="small" type="primary" @click="addItem" v-if="index===0"></el-button>
                  <el-button size="small" type="danger" @click="removeItem(item.key)"  v-if="index!==0"></el-button>
                </td>
              </tr>
            </table>
          </el-form-item>
          <el-form-item>
            <el-button size="small" type="primary" @click="somAct('form')"></el-button>
            <el-button size="small" type="primary" @click="goListShow"></el-button>
          </el-form-item>
        </el-form>
      </el-card>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    var validName = (rule, value, callback) => {
      if( !this.form.actName ) {
        callback(new Error(''));
      } else if( this.form.actName.length >= 30 ) {
        this.form.actName = this.form.actName.substr(0, 30)
        callback(new Error('30'));
      } else {
        callback();
      }
    };
    var validNote = (rule, value, callback) => {
      if( !this.form.note ) {
        callback(new Error(''));
      } else if( this.form.note.length >= 500 ) {
        this.form.note = this.form.note.substr(0, 500)
        callback(new Error('200'));
      } else {
        callback();
      }
    };
    var validHtime = (rule, value, callback) => {
      if( !this.form.hstimeStr || !this.form.hetimeStr ) {
        callback(new Error(''));
      } else if( this.form.hstimeStr >= this.form.hetimeStr ) {
        callback(new Error(''));
      } else {
        callback();
      }
    };
    var validActtime = (rule, value, callback) => {
      if( !this.form.actStimeStr || !this.form.actEtimeStr ) {
        callback(new Error(''));
      } else if( this.form.actStimeStr >= this.form.actEtimeStr ) {
        callback(new Error(''));
      } else {
        callback();
      }
    };
    return {
      loading: false,
      business: 1,
      search: {
        pageNo: 1,
        pageSize: 10,
        name: '',
        htype: ''
      },
      pagination: {
        total: 0
      },
      tableList: [],
      form: {
        id: '',
        actName: '',
        note: '',
        htype: 1,
        status: 1,
        hstimeStr: '',
        hetimeStr: '',
        actStimeStr: '',
        actEtimeStr: '',
        exchanges: [{id: '', srange: '', num: '', key: new Date().Format('yyyyMMddhhmmssS') + Math.random()}]
      },
      rules: {
        actName: [{required:true, validator:validName, trigger:'change'}],
        note: [{required:true, validator:validNote, trigger:'change'}],
        hstimeStr: [{required:true, validator:validHtime, trigger:'blur'}],
        hetimeStr: [{required:true, validator:validHtime, trigger:'blur'}],
        actStimeStr: [{required:true, validator:validActtime, trigger:'blur'}],
        actEtimeStr: [{required:true, validator:validActtime, trigger:'blur'}]
      }
    }
  },
  created() {
    this.list()
  },
  methods: {
    resetSearch () {
      this.search = {pageNo: 1, pageSize: 10, name: '', htype: ''}
      this.list()
    },
    initPagination(page) {
      if(page) {
        this.pagination.total = page.count
      }
    },
    list () {
      this.business = 1
      this.$request.post('/lsh/seller-manager/help/list', this.search, true, (res)=>{
        if (res.ok) {
          let datas = res.data.list || []
          this.tableList = datas
          this.initPagination(res.data.page || {})
        } else {
          this.$message.error(res.msg)
        }
      })
    },
    edit (idx, row) {
      this.$request.post('/lsh/seller-manager/help/detail', {id: row.id}, true, (res)=>{
        if (res.ok) {
          this.business = 2
          let datas = res.data || {}
          let exchanges = datas.exchanges || []
          if (exchanges.length > 0) {
            for (let i=0; i<exchanges.length; i++) {
              exchanges[i].key = new Date().Format('yyyyMMddhhmmssS') + Math.random()
            }
          }
          this.form = datas;
          this.form.hstime = ''
          this.form.hetime = ''
          this.form.actStime = ''
          this.form.actEtime = ''
        } else {
          this.$message.error(res.msg)
        }
      })
    },
    mstatus (id, val) {
      this.$request.post('/lsh/seller-manager/help/mstatus', {id: id, status: val}, true, (res)=>{
        if (res.ok) {
          this.$message({type:'success', message:""});
          this.list()
        } else {
          this.$message.error(res.msg)
        }
      })
    },
    resetForm () {
      this.form = {
        id: '',
        actName: '',
        note: '',
        htype: 1,
        status: 1,
        hstimeStr: '',
        hetimeStr: '',
        actStimeStr: '',
        actEtimeStr: '',
        exchanges: [{id: '', srange: '', num: '', key: new Date().Format('yyyyMMddhhmmssS') + Math.random()}]
      }
      this.$refs['form'].clearValidate()
    },
    goListShow () {
      this.list()
      this.resetForm()
    },
    curAct () {
      this.$request.post('/lsh/seller-manager/help/curAct', {}, true, (res)=>{
        if (res.ok) {
          let datas = res.data || {}
          let exchanges = datas.exchanges || []
          if (exchanges.length > 0) {
            for (let i=0; i<exchanges.length; i++) {
              exchanges[i].key = new Date().Format('yyyyMMddhhmmssS') + Math.random()
            }
          }
          this.form = datas;
          this.form.hstime = ''
          this.form.hetime = ''
          this.form.actStime = ''
          this.form.actEtime = ''
        } else {
          this.$message.error(res.msg)
        }
      })
    },
    somAct (form) {
      let exchanges = this.form.exchanges;
      if(exchanges.length==0) {
        this.$message.error("")
        return false
      }
      for(let i=0; i<exchanges.length; i++) {
        let item = exchanges[i]
        if(!item.srange||!item.num||item.srange<1||item.num<1) {
          this.$message.error(""+(i+1)+"0")
          return false
        }
      }
      this.$refs[form].validate((valid) => {
        if (valid) {
          this.$request.post('/lsh/seller-manager/help/somAct', this.form, true, (res)=>{
            if (res.ok) {
              this.$message({type:'success', message:""});
              this.list()
              this.resetForm()
            } else {
              this.$message.error(res.msg)
            }
          })
        }
      })
    },
    // 
    addItem () {
      // awardType3
      let item = {id: '', srange: '', num: ''}
      item.key = new Date().Format("yyyyMMddhhmmssS") + Math.random();
      this.form.exchanges.push( item );
    },
    // 
    removeItem (key) {
      if (this.form.exchanges.length<=1) {
        this.$message.error('')
        return false
      }
      console.log(key)
      for (let i=0; i<this.form.exchanges.length; i++) {
        if (this.form.exchanges[i].key==key) {
          this.form.exchanges.splice(i, 1)
        }
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  .space {position:relative;width:100%;height:20px;}
  .pl36 {padding-left:36px;}
  .form-note {font-size: 12px;color:#aaa;line-height:20px;}
  .el-input, .el-textarea {
    width: 600px;
  }
  .el-date-editor {
    width: 288px;
  }
  .search-block {
    .el-input {
      width: 400px;
    }
  }
  .award-table {
    margin-top: 10px;
    width: 600px;
    text-align: center;
    border-color:#ebeef5;
    border-collapse: collapse;
    color: #707478;
    thead {
      background: #ebeef5;
    }
    td {
      width: 33%;
      input {
        color: #888;
        outline:none;
        text-align: center;
        width: 90%;
        height: 24px;
        padding: 0 5px;
        border: none;
        background:transparent;
      }
      input::focus {
        outline:none;
        background-color: transparent;
      }
      input::selection{background:transparent;}
      input::-moz-selection{background:transparent;}
      input.one-cell-two {
        width: 35%;
      }
      .el-button {
        padding: 5px 15px;
      }
    }
  }
</style>



