<template>
  <div class="seller-manage-container">
    <div class="box-container">
<!--      <div class="add-new-seller clearfix">-->
<!--        <el-button type="primary" size="small"  @click="addNewSeller()"></el-button>-->
<!--      </div>-->
<!--      <div style="height:20px"></div>-->
      <!---->
      <el-col :span="24" class="toolbar" style="padding-bottom: 0px;margin-bottom: 0">
        <el-form :inline="true" :model="filters" label-width="90px">
<!--          <el-form-item :size="'small'" label="">-->
<!--            <el-select-->
<!--                v-model="filters.status"-->
<!--                placeholder=""-->
<!--                style="width: 200px">-->
<!--              <el-option-->
<!--                  v-for="item in statusList"-->
<!--                  :key="item.code"-->
<!--                  :label="item.name"-->
<!--                  :value="item.code">-->
<!--              </el-option>-->
<!--            </el-select>-->
<!--          </el-form-item>-->
          <el-form-item :size="'small'" label="" style="display:none">
            <el-select
                v-model="filters.commercial"
                placeholder=""
                style="width: 200px">
              <el-option
                  v-for="item in commercialList"
                  :key="item.code"
                  :label="item.name"
                  :value="item.code">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="saleZoneCode">
            <el-select size="small" v-model="filters.saleZoneCode" placeholder="">
              <el-option
                  v-for="(item,index) in saleZone"
                  :key="index"
                  :label="item.zoneName"
                  :value="item.zoneCode">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item :size="'small'" label="" >
            <el-select
                v-model="filters.areaType"
                placeholder=""
                style="width: 200px">
              <el-option
                  v-for="item in districtList"
                  :key="item.code"
                  :label="item.name"
                  :value="item.code">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item :size="'small'" label="">
            <el-input placeholder="" v-model="filters.keyword" class="input-with-select" style="width: 300px">
              <el-select v-model="filters.keywordType" slot="prepend" @change="inputWithSelectChange" placeholder="" style="width: 120px">
                <el-option label="" value="1"></el-option>
                <el-option label="" value="2"></el-option>
                <el-option label="" value="3"></el-option>
                <el-option label="" value="4"></el-option>
                <el-option label="" value="5"></el-option>
              </el-select>
            </el-input>
          </el-form-item>
          <el-form-item :size="'small'" label="">
            <el-date-picker
                v-model="filters.time"
                type="datetimerange"
                placeholder=""
                value-format="yyyy-MM-dd HH:mm:ss"
                range-separator=""
                start-placeholder=""
                end-placeholder=""
                style="width: 350px">
            </el-date-picker>
          </el-form-item>
          <el-form-item size="small" label="" prop="provCode">
            <el-select
                v-model="filters.provCode"
                placeholder=""
                @change="selectBrand1"
                style="width: 160px">
              <el-option
                  v-for="item in cateLvl1List"
                  :key="item.code"
                  :label="item.name"
                  :value="item.code">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item size="small" label="" prop="cityCode">
            <el-select
                v-model="filters.cityCode"
                placeholder=""
                @change="selectBrand2"
                style="width: 160px">
              <el-option
                  v-for="item in cateLvl2List"
                  :key="item.code"
                  :label="item.name"
                  :value="item.code">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item size="small" label="" prop="countyCode">
            <el-select
                v-model="filters.countyCode "
                placeholder=""
                style="width: 160px">
              <el-option
                  v-for="item in cateLvl3List"
                  :key="item.code"
                  :label="item.name"
                  :value="item.code">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item class="large" :size="'small'" label="" style="display:none">
            <el-select
                v-model="filters.isPrint"
                placeholder=""
                style="width: 200px">
              <el-option
                  v-for="item in isPrintList"
                  :key="item.code"
                  :label="item.name"
                  :value="item.code">
              </el-option>
            </el-select>
          </el-form-item>
          <div></div>
          <el-form-item class="mr0" :size="'small'">
            <el-button type="primary" size="small" @click="commitForm"></el-button>
            <el-button size="small" class="important" @click="getStatus"></el-button>
            <!--
            <el-button type="primary" size="small" @click="getListNewList(1)"></el-button>
            <el-button type="primary" size="small" @click="getListNewList(2)"></el-button>
            <el-button type="primary" size="small" @click="getListNewList(3)"></el-button>
            <el-button type="primary" size="small" @click="getListNewList(5)"></el-button>
            -->
<!--            <el-button type="primary" size="small" @click="exportSeller"></el-button>-->
          </el-form-item>
        </el-form>
      </el-col>
    </div>

    <div class="box-container">
      <!---->
      <el-col class="crm-table-wrap" v-loading="listLoading" :span="24">
        <el-table
            @selection-change="handleSelectionChange"
            :data="newList"
            style="width: 100%">
          <el-table-column
              v-if="showSelection"
              type="selection"
              width="55">
          </el-table-column>
          <el-table-column
              type="index"
              label=""
              width="50">
          </el-table-column>
          <el-table-column
              prop="licenseNo"
              label="	"
              width="200">
          </el-table-column>
          <el-table-column
              prop="shopName"
              label=""
              width="120">
          </el-table-column>
          <el-table-column
              prop="contactName"
              label=""
              width="120">
          </el-table-column>
          <el-table-column
              prop="areaType"
              label=""
              width="120">
            <template slot-scope="scope">
              <span v-if="scope.row.areaType == '1' ">{{ "" }}</span>
              <span v-if="scope.row.areaType == '2' ">{{ "" }}</span>
            </template>
          </el-table-column>
<!--          <el-table-column-->
<!--              prop="commercial"-->
<!--              label=""-->
<!--              width="120">-->
<!--            <template slot-scope="scope">-->
<!--              <span v-if="scope.row.commercial == '1'">{{ "" }}</span>-->
<!--              <span v-else-if="scope.row.commercial == '2'">{{ "" }}</span>-->
<!--              <span v-else-if="scope.row.commercial == '3'">{{ "" }}</span>-->
<!--              <span v-else-if="scope.row.commercial == '4'">{{ "" }}</span>-->
<!--              <span v-else-if="scope.row.commercial == '5'">{{ "" }}</span>-->
<!--              <span v-else-if="scope.row.commercial == '6'">{{ "" }}</span>-->
<!--              <span v-else-if="scope.row.commercial == '7'">{{ "" }}</span>-->
<!--            </template>-->
<!--          </el-table-column>-->
          <el-table-column
              prop="detailAddr"
              label=""
              width="120">
            <template slot-scope="scope">
              {{ scope.row.provName + scope.row.cityName + scope.row.countyName + scope.row.detailAddr }}
            </template>
          </el-table-column>
          <el-table-column
              prop="contactPhone"
              label=""
              width="120">
          </el-table-column>
          <el-table-column
              prop="salesman"
              label="">
          </el-table-column>
          <el-table-column property="saleZoneCode" label="" align="center" width="180px">
            <template slot-scope="scope">
              {{ getSaleZoneName(scope.row.saleZoneCode) }}
            </template>
          </el-table-column>
          <el-table-column
              prop="ctime"
              label="">
          </el-table-column>
          <!--
          <el-table-column
              prop="score"
              label="">
          </el-table-column>
          -->
<!--          <el-table-column sortable="custom"-->
<!--              prop="passTime"-->
<!--              label=""-->
<!--              width="200">-->
<!--            <template slot-scope="scope">-->
<!--              <span>{{ new Date(scope.row.applyTime).Format('yyyy-MM-dd hh:mm:ss')}}</span>-->
<!--            </template>-->
<!--          </el-table-column>-->
          <!--
          <el-table-column
              prop="printQrCodeCount"
              label="">
            <template slot-scope="scope">
              <span v-if="scope.row.printQrCodeCount && scope.row.printQrCodeCount > 0">{{ "" }}</span>
              <span v-else>{{ "" }}</span>
            </template>
          </el-table-column>
          <el-table-column
              prop="printQrCodeCount"
              label="">
            <template slot-scope="scope">
              <span v-if="scope.row.printQrCodeCount == null">{{ "0" }}</span>
              <span v-else>{{ scope.row.printQrCodeCount }}</span>
            </template>
          </el-table-column>

          <el-table-column
              prop="scanCount"
              label="">
          </el-table-column>
          <el-table-column
              prop="fansCount"
              label="">
          </el-table-column>
          <el-table-column
              prop="balance"
              label="">
            <template slot-scope="scope">
              <span >{{ `${scope.row.wallet.balance}` }}</span>
            </template>
          </el-table-column>
          -->
          <el-table-column
              prop="status"
              label="">
            <template slot-scope="scope">
              <span v-if="scope.row.status == '1'">{{ "" }}</span>
              <span v-else-if="scope.row.status == '2'">{{ "" }}</span>
              <span v-else-if="scope.row.status == '3'">{{ "" }}</span>

            </template>
          </el-table-column>
          <el-table-column
              fixed="right"
              label=""
              width="150"
          >
            <template slot-scope="scope" >
              <el-button
                  type="text"
                  size="mini"
                  @click="sellerDetail(scope.row.id)"
              ></el-button>
<!--              <el-button-->
<!--                  v-if="scope.row.status == '2'"-->
<!--                  type="text"-->
<!--                  size="mini"-->
<!--                  @click="approval(scope.row.id)"-->
<!--              ></el-button>-->
<!--              <el-button-->
<!--                  v-if="scope.row.status == '2'"-->
<!--                  type="text"-->
<!--                  size="mini"-->
<!--                  @click="disapprovalDialog(scope.row.id)"-->
<!--              ></el-button>-->
            </template>
          </el-table-column>
        </el-table>
      </el-col>

      <!---->
      <el-col :span="24" class="footer-toolbar">
        <el-button
            v-if="showSelection"
            type="primary"
            size="small"
            :disabled="batchOperations"
            @click="modifyAuthOrder(ids,true)"
        ></el-button>
        <!--<el-button type="primary" class="btn-jump-to" @click="jumpTo"></el-button>-->
        <el-pagination
            background
            layout="total,prev, pager, next,jumper"
            :current-page="currentPage"
            @current-change="handleCurrentChange"
            :page-size="pageSize"
            :total="total"
            style="float:right;">
        </el-pagination>
      </el-col>

      <el-dialog
          title=""
          :visible.sync="dialogVisible"
          width="30%"
          :before-close="cancelDialog">
        <el-input
            type="textarea"
            :rows="2"
            placeholder=""
            v-model="failReason">
        </el-input>
        <div style="text-align: center;margin-top: 30px">
          <el-button size="small" @click="cancelDialog"> </el-button>
          <el-button size="small" type="primary" @click="disapproval"> </el-button>
        </div>

      </el-dialog>
    </div>
  </div>
</template>

<script>
  export default {
    name: "SellerManage",
    data(){
      return{
        showSelection:false,
        ids:'',
        batchOperations:true,
        //
        dialogVisible:false,
        failReason:'',
        txId:'',
        saleZone:[],
        listLoading:false,

        //
        statusList:[
          {
            code:"1",
            name:""
          },
          {
            code:"2",
            name:" "
          },
          {
            code:"3",
            name:""
          },
        ],
        commercialList:[
          {
            code:"",
            name:""
          },
          {
            code:"1",
            name:""
          },
          {
            code:"2",
            name:""
          },
          {
            code:"3",
            name:""
          },
          {
            code:"4",
            name:""
          },
          {
            code:"5",
            name:""
          },
          {
            code:"6",
            name:""
          },
          {
            code:"7",
            name:""
          },
        ],
        districtList:[
          {
            code:"",
            name:""
          },
          {
            code:"1",
            name:""
          },
          {
            code:"2",
            name:""
          },
        ],
        isPrintList:[
          {
            code:"",
            name:""
          },
          {
            code:"0",
            name:""
          },
          {
            code:"1",
            name:""
          },

        ],

        //
        cateLvl1List:[],
        cateLvl2List:[],
        cateLvl3List:[],
        saleZoneCode: sessionStorage.getItem('isAllSaleZone') == 1 ? null : sessionStorage.getItem('saleZoneCode'),
        filters: {
          saleZoneCode: sessionStorage.getItem('isAllSaleZone') == 1 ? null : sessionStorage.getItem('saleZoneCode'),
          //
          status:'1',
          //1: , 2: , 3: 
          authStatus :'4',
          // : 1-2-3-4-5-6-7-
          commercial:'',
          //: 1-2-
          areaType:'',
          //  1-2-3-4-5-
          keywordType:null,
          //
          keyword:null,
          //
          startDate:'',
          // //
          endDate:'',
          time:[],

          //
          provCode:null,
          //
          cityCode:null,
          //
          countyCode:null,
          //
          isPrint:'',

          //
          sortType:1,
          sortValue:1


        },
        //
        newList:[],

        //
        pageSize:10,

        pageNo: 1,
        //
        currentPage:1,
        //
        total:0,

      }

    },
    created(){
      this.getSaleZone()
      this.getListNewList()
      this.getOneCategory()
    },
    methods:{
      modifyAuthOrder(ids,batch) {
        let params = {idList:ids}
        let _batch = ""
        if(batch){
          _batch = ""
        }
        this.$confirm(`${_batch}`, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          // this.$request.post('/sc/saotx/mall/order/authOrder',params,true,res => {
          this.$request.post('/saasHbseller/audit/agree',params,false,res => {
            if(res.code == "200" && !res.data.failList.length){
              this.$message({
                message: '',
                type: 'success'
              });
              this.getListNewList()
            }else{
              this.$message({
                message: res.msg || `id ${res.data.failList.join(',')}`,
                type: 'warning'
              });
            }
          })
        })
      },
      handleSelectionChange(val) {
        let arr = val
        console.log(arr)
        let idsArr = []
        if(arr.length > 0 ){
          this.batchOperations = false
          arr.map((e,i) => {
            idsArr.push(e.id)
          })
          this.ids = idsArr.join(",")
        }else{
          this.ids = ""
          this.batchOperations = true
        }

      },
      getSaleZone() {
        this.$request.post('/api/saleZone/userSzList', {}, true, (res) => {
          if (res.code == '200') {
            this.saleZone = res.data || []
            if (this.isAllSaleZone == 1) {
            } else {
              this.filters.saleZoneCode = this.saleZoneCode
            }
          }
        })
      },
      getSaleZoneName(saleZoneCode){
        if(!saleZoneCode) return ""
        let zoneName = ""
        let item = this.saleZone.find((i) => {
          if(i.zoneCode == saleZoneCode){
            return i
          }
        })
        // console.log(item)
        zoneName = item ? item.zoneName : ''
        return zoneName
      },
      addNewSeller(){
        this.$router.push({
          'path':'/seller/mgr/addSeller'
        })
      },
      //1
      getOneCategory(){
        this.$request.post('/saasHbseller/region/province',{}, true, (res) => {
          this.cateLvl1List = [...res]
        })
      },
      selectBrand1(){
        this.filters.cityCode = ''
        this.cateLvl2List = []
        this.filters.countyCode = ''
        this.cateLvl3List = []
        this.getTwoCategory()
      },
      getTwoCategory(){
        if(this.filters.provCode  == '')return
        this.$request.post('/saasHbseller/region/city',{parentCode:this.filters.provCode}, false, (res) => {
          this.cateLvl2List = [...res]
        })
      },
      selectBrand2(){
        this.filters.countyCode = ''
        this.cateLvl3List = []
        this.getThreeCategory()
      },
      //3
      getThreeCategory(){
        if(this.filters.cityCode == '')return
        this.$request.post('/saasHbseller/region/district',{parentCode:this.filters.cityCode}, false, (res) => {
          this.cateLvl3List = [...res]
        })
      },
      // //
      // sortBySales(){
      //   this.getListJD("bySales")
      // },
      // sortByScore(){
      //   this.getListJD("byScore")
      // },
      // sortByTime(){
      //   this.getListJD("byTime")
      // },
      sortDatasChange({column, prop, order}) {
        if("descending"==order) {
          this.filters.sortValue = 1;
        } else {
          this.filters.sortValue = 0;
        }
        this.getListNewList(1);
      },
      //
      getListNewList(type) {
        let params = {
          // status:this.filters.status,
          saleZoneCode:this.filters.saleZoneCode,
          //
          // commercial:this.filters.commercial,
          //
          areaType:this.filters.areaType,

          keywordType:this.filters.keywordType,
          //
          keyword:this.filters.keyword,

          //
          provCode: this.filters.provCode,
          cityCode: this.filters.cityCode,
          countyCode: this.filters.countyCode,
          //
          startDate: this.filters.time?this.filters.time[0]?this.filters.time[0]:'':'',
          endDate: this.filters.time?this.filters.time[1]?this.filters.time[1]:'':'',

          // isPrint:this.filters.isPrint,
          //
          // sortType: 1,
          // sortValue: this.filters.sortValue,

          page: this.pageNo,
          pageSize: 10,

        };
        // if(type){
        //   params.sortType = type
        //   if(this.filters.sortValue==1){
        //     this.filters.sortValue = 0
        //   }else {
        //     this.filters.sortValue = 1
        //   }
        // }
        // if(this.filters.status == 4){
        //   //
        //   params.authStatus = this.filters.authStatus
        // }else{
        //   params.status = this.filters.status
        // }

        // 
        // if(this.filters.searchType == '3') {
        //   params.licenceNo = this.filters.keywords || ''; // 
        // } else if(this.filters.searchType == '1') {
        //   params.ownerName = this.filters.keywords || ''; // 
        // } else if(this.filters.searchType == '2') {
        //   params.phoneNo = this.filters.keywords || ''; // 
        // } else if(this.filters.searchType == '4') {
        //   params.shopName = this.filters.keywords || ''; // 
        // } else if(this.filters.searchType == '5') {
        //   params.salesmanName = this.filters.keywords || ''; // 
        // }

        this.postSearch(params)
      },
      postSearch(params) {
        // this.listLoading = true;
        this.$request.post('/saasHbseller/seller/manager/queryList', params, true, (res) => {

            console.log(res)
            // this.listLoading = false;
            this.newList = res.data.infoList.records
            this.total = res.data.infoList.total
            this.pageNo = res.data.infoList.current
            if(params.status == 2){
              this.showSelection = true
            }else {
              this.showSelection = false
            }

        })
      },
      //
      commitForm() {
        this.pageNo = 1
        this.currentPage = 1
        this.getListNewList()
      },
      //
      getStatus() {
        //1-2-3-
        this.filters.status = '1'
        this.filters.saleZoneCode = this.saleZoneCode
        //: 1-2-
        this.filters.areaType = ''
        //  1-2-3-4-5-
        this.filters.keywordType = ''
        //
        this.filters.keyword = ''

        this.filters.time = [];
        //
        this.filters.startDate = ''
        //
        this.filters.endDate = ''

        //
        this.filters.provCode = null
        //
        this.filters.cityCode = null
        //
        this.filters.countyCode = null

        this.filters.status = '1'
        this.filters.isPrint = ''

        this.pageNo = 1
        this.currentPage = 1
        this.getListNewList()
      },
      inputWithSelectChange(val){
        this.filters.keywords = ""
      },

      //
      approval(sellerId){
        let params = {idList:sellerId}
        this.$confirm(``, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$request.post('/saasHbseller/audit/agree',params,false,res => {
            if(res.code == "200" && !res.data.failList.length){
              this.$message({
                message: '',
                type: 'success'
              });
              this.getListNewList()
            }else{
              this.$message({
                message: res.msg || `id ${res.data.failList.join(',')}`,
                type: 'warning'
              });
            }
          })
        })
      },

      //
      disapproval(){
        let params = {id:this.txId,appStatus:2,auditMsg:this.failReason}
        this.$confirm(``, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$request.post('/saasHbseller/audit/unAgree',params,false,res => {
            if(res.code == 200){
              this.$message({
                message: '',
                type: 'success'
              });
              this.dialogVisible = false
              this.failReason = ""
              this.getListNewList()
            }else{
              console.log(res)
              this.$message({
                message: res.msg,
                type: 'warning'
              });
              this.dialogVisible = false
              this.failReason = ""
              this.getListNewList()
            }
          })
        })
      },
      //
      disapprovalDialog(id){
        this.dialogVisible = true
        this.txId = id
      },
      cancelDialog(){
        this.dialogVisible = false
        this.failReason = ""
      },
      sellerDetail(sellerId){
        this.$router.push({
          path:'/seller/mgr/sellerDetail?sellerId='+sellerId
        })
      },

      //
      handleCurrentChange(val) {
        this.pageNo = val
        this.currentPage = val
        this.getListNewList()
      },
      exportSeller(){
        let params = {
          //
          status:this.filters.status,
          saleZoneCode:this.filters.saleZoneCode,
          //
          areaType:this.filters.areaType,

          keywordType:this.filters.keywordType,
          //
          keyword:this.filters.keyword,

          //
          provCode: this.filters.provCode,
          cityCode: this.filters.cityCode,
          countyCode: this.filters.countyCode,
          //
          startDate: this.filters.time?this.filters.time[0]?this.filters.time[0]:'':'',
          endDate: this.filters.time?this.filters.time[1]?this.filters.time[1]:'':'',

          isPrint:this.filters.isPrint,
          //
          sortType: 1,
          sortValue: this.filters.sortValue,

          pageNo: this.pageNo,
          pageSize: 10,
        };
        if(this.filters.status == 4){
          //
          params.authStatus = this.filters.authStatus
        }else{
          params.status = this.filters.status
        }
        // 
        // if(this.filters.searchType == '3') {
        //   params.licenceNo = this.filters.keywords || ''; // 
        // } else if(this.filters.searchType == '1') {
        //   params.ownerName = this.filters.keywords || ''; // 
        // } else if(this.filters.searchType == '2') {
        //   params.phoneNo = this.filters.keywords || ''; // 
        // } else if(this.filters.searchType == '4') {
        //   params.shopName = this.filters.keywords || ''; // 
        // } else if(this.filters.searchType == '5') {
        //   params.salesmanName = this.filters.keywords || ''; // 
        // }


        var xhr = new XMLHttpRequest();
        var formData = new FormData();
        for(var attr in params) {
          formData.append(attr, params[attr]);
        }
        xhr.overrideMimeType("text/plain; charset=x-user-defined");
        xhr.open('POST', '/lsh/seller-manager/seller/exportDatas', true);
        xhr.responseType = "arraybuffer";
        xhr.setRequestHeader("token", sessionStorage.getItem('access_token'));
        xhr.setRequestHeader("loginId", sessionStorage.getItem('access_loginId'));
        xhr.onload = function(res) {
          if (this.status == 200) {
            var blob = new Blob([this.response], {type: 'application/vnd.ms-excel'});
            var respHeader = xhr.getResponseHeader("Content-Disposition");
            var fileName = decodeURI(respHeader.match(/filename=(.*?)(;|$)/)[1]);
            if (window.navigator.msSaveOrOpenBlob) {
              navigator.msSaveBlob(blob, fileName);
            } else {
              var link = document.createElement('a');
              link.href = window.URL.createObjectURL(blob);
              link.download = fileName;
              link.click();
              window.URL.revokeObjectURL(link.href);
            }
          }
        }
        return xhr.send(formData);
      }
    }
  }
</script>

<style lang="scss" scoped>
  .seller-manage-container{
  .box-container{
    background-color: #fff;
    padding: 15px;
    margin-bottom: 30px;
    add-new-seller{
      margin-bottom: 30px;
    }
  &:after{
     content: '';
     display: block;
     width: 0;
     height: 0;
     clear: both;
   }
  }
  }
  .el-form-item {
    margin-bottom: 20px;
  }
</style>
<style >
  .seller-manage-container .el-table{text-align: center}
  .seller-manage-container .el-table th{text-align: center}
  .seller-manage-container .el-table td{text-align: center}
  .seller-manage-container .mini .el-form-item__label {width: 40px!important;}
  .seller-manage-container .large .el-form-item__label {width: 120px!important;}
</style>



