<template>
    <div>
        <!--  -->
        <div v-show="business==1">
            <el-card>
                <el-row>
                    <el-button size="small" type="primary" @click="business=2"></el-button>
                </el-row>
                <div class="space"></div>
                <!--  -->
                <el-form :inline="true" :model="form" class="search-block">
                    <el-form-item label="">
                        <el-input size="small" v-model="form.periodName" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-select size="small" v-model="form.status" placeholder="">
                            <el-option label="" value=""></el-option>
                            <el-option label="" value="2"></el-option>
                            <el-option label="" value="1"></el-option>
                            <el-option label="" value="0"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="">
                        <el-select size="small" v-model="form.achievementType" placeholder="">
                            <el-option label="" value=""></el-option>
                            <el-option label="" value="1"></el-option>
                            <el-option label="" value="2"></el-option>
                        </el-select>
                    </el-form-item>

                    <div></div>
                    <el-form-item>
                        <el-button size="small" type="primary" @click="list"></el-button>
                        <el-button size="small" @click="resetForm"></el-button>
                    </el-form-item>
                </el-form>
            </el-card>
            <div class="space"></div>
            <el-card>
                <!--  -->
                <el-table :data="tableList" style="width: 100%">
                    <el-table-column label="" type="index" align="center" width="80px">
                        <template slot-scope="scope">
                            {{ (form.pageNo-1)*form.pageSize + scope.$index + 1 }}
                        </template>
                    </el-table-column>
                    <el-table-column prop="periodName" label="" align="center"></el-table-column>
                    <el-table-column prop="achievementTypeName" label="" align="center"></el-table-column>
                    <el-table-column prop="statusName" label="" align="center"></el-table-column>
                    <el-table-column label="" align="center" width="300px">
                        <template slot-scope="scope">
                            {{scope.row.stimeStr}} -- {{scope.row.etimeStr}}
                        </template>
                    </el-table-column>
                    <el-table-column label="" align="center" width="200">
                        <template slot-scope="scope">
                            <el-button size="mini" @click="seeRank(scope.$index, scope.row)"></el-button>
                            <el-button size="mini" @click="confDetail(scope.$index, scope.row)"></el-button>
                        </template>
                    </el-table-column>
                </el-table>
                <div class="space"></div>
                <!--  -->
                <el-pagination background layout="total, prev, pager, next, jumper"
                    @size-change="sizeChange" @current-change="currentChange"
                    :current-page="form.pageNo" :page-size="form.pageSize"
                    :total="pagination.total">
                </el-pagination>
            </el-card>
        </div>

        <!--  -->
        <div v-show="business==2">
            <el-card>
                <el-form :model="dataForm" :rules="dataRules" ref="dataForm" label-width="140px">
                    <el-form-item label="" prop="periodName">
                        <el-input size="small" v-model="dataForm.periodName"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="achievementType">
                        <el-radio size="small" v-model="dataForm.achievementType" :label="1"></el-radio>
                        <el-radio size="small" v-model="dataForm.achievementType" :label="2"></el-radio>
                        <p class="form-note">
                            <br>
                            <span class="pl36"></span>
                        </p>
                    </el-form-item>
                    <el-form-item label="" prop="stimeStr">
                        <!--<el-date-picker size="small"
                            v-model="dataForm.datetime"
                            format="yyyy-MM-dd HH:mm:ss"
                            value-format="yyyy-MM-dd HH:mm:ss"
                            type="datetimerange"
                            range-separator=""
                            start-placeholder=""
                            end-placeholder="">
                        </el-date-picker>-->
                        <el-date-picker v-model="dataForm.stimeStr" type="datetime" value-format="yyyy-MM-dd HH:mm:ss" placeholder=""></el-date-picker>
                        
                        <el-date-picker v-model="dataForm.etimeStr" type="datetime" value-format="yyyy-MM-dd HH:mm:ss" placeholder=""></el-date-picker>
                    </el-form-item>
                    <el-form-item label="" prop="ruleHtml">
                        <el-input size="small" type="textarea" :rows="10" v-model="dataForm.ruleHtml" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="ID" prop="templateId">
                        <el-input size="small" v-model="dataForm.templateId"></el-input>
                        <p class="form-note">
                            IDID<br>
                            <span class="pl36">IDcmtrt_o4nyw5XZXxuCLUstPtNei2DzvYk-4uGZX5ldo</span>
                        </p>
                    </el-form-item>
                    <el-form-item label="">
                        <el-button size="small" type="primary" @click="addAwardItem"></el-button>
                        <el-button size="small" @click="removeAwardItem"></el-button>
                        <table class="award-table" border="1">
                            <thead>
                                <td></td>
                                <td></td>
                                <td></td>
                            </thead>
                            <tr v-for="item in dataForm.awards" :key="item.key">
                                <td>
                                    <input type="hidden" v-model="item.id"/>
                                    <input type="number" class="one-cell-two" v-model="item.rankStart"/>
                                    -
                                    <input type="number" class="one-cell-two" v-model="item.rankEnd"/>
                                </td>
                                <td>
                                    <input type="text" v-model="item.awardName"/>
                                </td>
                                <td>
                                    <input type="number" v-model="item.awardPrice"/>
                                </td>
                            </tr>
                        </table>
                    </el-form-item>
                    <div class="space"></div>
                    <el-form-item>
                        <el-button size="small" type="primary" @click="submitForm('dataForm')"></el-button>
                        <el-button size="small" @click="cancelForm"></el-button>
                    </el-form-item>
                </el-form>
            </el-card>
        </div>

        <!--  -->
        <div v-show="business==3">
            <el-card>
                <el-row>
                    <el-button type="text" @click="goTable"></el-button>
                </el-row>
                <div class="space"></div>
                <!--  -->
                <el-form :inline="true" :model="rankForm" class="search-block">
                    <el-form-item label="">
                        <el-select size="small" v-model="rankForm.rankSection" placeholder="">
                            <el-option label="" value=""></el-option>
                            <el-option v-for="item in awardConfList" :key="item.id" :label="item.rankStart+'-'+item.rankEnd" :value="item.rankStart+'-'+item.rankEnd"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input size="small" v-model="rankForm.shopName" placeholder=""></el-input>
                    </el-form-item>
                    <br/>
                    <el-form-item>
                        <el-button size="small" type="primary" @click="listRank"></el-button>
                        <el-button size="small" @click="resetRandForm(true)"></el-button>
                    </el-form-item>
                    <el-form-item>
                        <el-button size="small" type="primary" @click="openTplMsg"></el-button>
                        <el-button size="small" :type="canPay?'primary':''" :disabled="!canPay" @click="sendRankPay"></el-button>
                        <el-button size="small" :type="canExport?'primary':''" :disabled="!canExport" @click="exportDatas"></el-button>
                    </el-form-item>
                </el-form>
            </el-card>
            <div class="space"></div>
            <el-card>
                <!--  -->
                <el-table :data="tableRankList" style="width: 100%">
                    <el-table-column label="" type="index" align="center">
                        <template slot-scope="scope">
                            {{ (form.pageNo-1)*form.pageSize + scope.$index + 1 }}
                        </template>
                    </el-table-column>
                    <el-table-column prop="idx" label="" align="center"></el-table-column>
                    <el-table-column prop="shopName" label="" align="center" width="180"></el-table-column>
                    <el-table-column prop="ownerName" label="" align="center" width="100"></el-table-column>
                    <el-table-column prop="phoneNo" label="" align="center" width="120"></el-table-column>
                    <el-table-column prop="licenceNo" label="" align="center" width="120"></el-table-column>
                    <el-table-column label="" align="center" width="200">
                        <template slot-scope="scope">
                            {{scope.row.addrProvinceName}}{{scope.row.addrCityName}}{{scope.row.addrAreaName}}
                        </template>
                    </el-table-column>
                    <el-table-column prop="addrDetail" label="" align="center"></el-table-column>
                    <el-table-column prop="achieveNum" label="" align="center" width="150"></el-table-column>
                    <el-table-column prop="awardName" label="" align="center" width="120"></el-table-column>
                    <el-table-column label="" align="center">
                        <template slot-scope="scope">
                             {{scope.row.price}}
                        </template>
                    </el-table-column>
                    <el-table-column prop="payStatusName" label="" align="center"></el-table-column>
                    <el-table-column label="" align="center" width="100">
                        <template slot-scope="scope">
                            <el-button size="mini" @click="seeSellerDetail(scope.$index, scope.row)"></el-button>
                        </template>
                    </el-table-column>
                </el-table>
                <div class="space"></div>
                <!--  -->
                <el-pagination background layout="total, prev, pager, next, jumper"
                    @size-change="sizeChange" @current-change="currentChange"
                    :current-page="form.pageNo" :page-size="form.pageSize"
                    :total="pagination.total">
                </el-pagination>
            </el-card>
        </div>

        <el-dialog center title="" :visible.sync="tplMsgForm.show" width="60%" @closed="resetTplMsgForm">
            <div>
                <el-form class="tplM-msg-block" :rules="tplMsgRules" ref="tplMsgForm"  :model="tplMsgForm" label-width="100px">
                    <el-form-item label="" prop="first">
                        <el-input size="small" type="textarea" v-model="tplMsgForm.first" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="keyword1">
                        <el-input size="small" v-model="tplMsgForm.keyword1" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="keyword2">
                        <!--<el-date-picker v-model="tplMsgForm.sendTime" type="daterange" range-separator="" start-placeholder="" end-placeholder=""></el-date-picker>-->
                        <el-input size="small" v-model="tplMsgForm.keyword2" placeholder="20190101-20190131"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="remark">
                        <el-input size="small" type="textarea" v-model="tplMsgForm.remark" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="sendScope">
                        <el-radio v-model="tplMsgForm.sendScope" :label="1"></el-radio>
                        <el-radio v-model="tplMsgForm.sendScope" :label="2"></el-radio>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input size="small" v-model="tplMsgForm.templateUrl" placeholder=""></el-input>
                    </el-form-item>
                </el-form>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button size="small" type="primary" @click="confirmSendMsg('tplMsgForm')"> </el-button>
                <el-button size="small" @click="resetTplMsgForm"> </el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
export default {
    data() {
        var validDatetime = (rule, value, callback) => {
            if( !this.dataForm.stimeStr || !this.dataForm.etimeStr ) {
                callback(new Error(''));
            } else if( this.dataForm.stimeStr >= this.dataForm.etimeStr ) {
                callback(new Error(''));
            } else {
                callback();
            }
        };
        return {
            loading: false,
            business: 1, // 1-2-form3-
            requesting: true, // 
            form: {
                pageNo: 1,
                pageSize: 10,
                periodName: "",
                status: "",
                achievementType: ""
            },
            pagination: {
                total: 0
            },
            tableList: [],

            dataForm: {
                id: "",
                periodName: "",
                achievementType: 1,
                ruleHtml: "",
                datetime: [],
                stimeStr: "",
                etimeStr: "",
                templateId: "",
                topNum: "100",
                awards: []
            },
            dataRules: {
                periodName: [{required:true, message:'', trigger:'blur'}],
                achievementType: [{required:true, message:'', trigger:'blur'}],
                periodName: [{required:true, message:'', trigger:'blur'}],
                ruleHtml: [{required:true, message:'', trigger:'blur'}],
                //datetime: [{required:true, message:'', trigger:'blur'}],
                stimeStr: [{required:true, validator:validDatetime, trigger:'blur'}],
                etimeStr: [{required:true, validator:validDatetime, trigger:'blur'}],
                templateId: [{required:true, message:'ID', trigger:'blur'}]
            },

            awardConfList: [], // 
            rankForm: {
                pageNo: 1,
                pageSize: 10,
                periodId: "",
                rankSection: "",
                shopName: ""
            },
            canPay: false, // 
            canExport: true, // 
            tableRankList: [],

            // 
            tplMsgForm: {
                show: false,
                first: '',
                keyword1: '',
                keyword2: '',
                remark: '',
                templateUrl: '',
                sendScope: 2
            },
            tplMsgRules: {
                sendScope: [{required:true, message:'', trigger:'blur'}],
                first: [{required:true, message:'', trigger:'blur'}],
                keyword1: [{required:true, message:'', trigger:'blur'}],
                keyword2: [{required:true, message:'', trigger:'blur'}],
                remark: [{required:true, message:'', trigger:'blur'}]
            },
            sendMsg: false // 
        }
    },
    created() {
        this.list();
    },
    methods: {
        currentChange(pageNo) {
            // pageNo
            if(this.business==1) {
                this.list(event, pageNo);
            } else if(this.business==3) {
                this.listRank(event, pageNo);
            }
        },
        sizeChange(pageSize) {
            // pageSize\
            if(this.business==1) {
                this.list(event, null, pageSize);
            } else if(this.business==3) {
                this.listRank(event, null, pageSize);
            }
        },
        // page = {"pageCount":, "count":}
        initPagination(page) {
            if(page) {
                this.pagination.total = page.count;
            }
        },
        // 
        resetForm() {
            this.form = {
                pageNo: 1,
                pageSize: 10,
                periodName: "",
                status: "",
                achievementType: ""
            };
            this.list();
        },
        // 
        list(_event, pageNo, pageSize) {
            let _pageNo = 1;
            if(pageNo) _pageNo = pageNo;
            this.form.pageNo = _pageNo;
            let _pageSize = 10;
            if(pageSize) _pageSize = pageSize;
            this.form.pageSize = _pageSize;

            this.loading = true;
            this.$request.post('/lsh/seller-manager/achieve/queryList', this.form, true, (res)=>{
                this.loading = false;
                if (res.ok) {
                    this.tableList = res.data.list || [];
                    this.initPagination(res.data.page||{});
                } else {
                    this.$message.error(res.msg);
                }
            });
        },
        // 
        seeRank(index, row) {
            this.rankForm.periodId = row.id;
            this.periodAwardList();
            this.listRank();
            this.judgePay();
            this.business = 3;
        },
        // 
        confDetail(index, row) {
            this.getConfDetail(row.id);
        },
        seeSellerDetail(index, row) {
            this.$router.push({
                path:'/seller/mgr/sellerDetail?sellerId='+row.sellerId
            })
        },
        goTable() {
            this.list();
            this.business = 1;
            this.resetRandForm(false);
        },
        // id
        getConfDetail(periodId) {
            this.$request.post('/lsh/seller-manager/achieve/configDetail', {periodId: periodId}, true, (res)=>{
                if (res.ok) {
                    this.business = 2;
                    this.dataForm = res.data || {};
                    this.$set(this.dataForm, 'datetime', [this.dataForm.stimeStr, this.dataForm.etimeStr]);
                } else {
                    this.$message.error(res.msg);
                }
            });
        },
        // 
        addAwardItem() {
            // awardType3
            let awardItem = {id:"",periodId:"",awardType:3,rankStart:"",rankEnd:"",awardName:"",awardPrice:""};
            awardItem.key = new Date().Format("yyyyMMddhhmmssS") + Math.random();
            this.dataForm.awards.push( awardItem );
        },
        // 
        removeAwardItem() {
            let awards = this.dataForm.awards;
            if(awards.length>0) {
                awards.splice(awards.length-1, 1);
            }
        },
        // 
        submitForm(form) {
            let awards = this.dataForm.awards;
            if(awards.length==0) {
                this.$message.error("");
                return false;
            }
            for(let i=0;i<awards.length;i++) {
                let item = awards[i];
                if(!item.rankStart||!item.rankEnd||item.rankStart<1||item.rankEnd<1) {
                    this.$message.error(""+(i+1)+"0");
                    return false;
                }
                if(!item.awardName) {
                    this.$message.error(""+(i+1)+"");
                    return false;
                }
                if(!item.awardPrice||item.awardPrice<=0) {
                    this.$message.error(""+(i+1)+"0");
                    return false;
                }
                item.periodId = this.dataForm.id;
            }
            this.$refs[form].validate((valid) => {
                if (valid) {
                    //this.dataForm.stimeStr = this.dataForm.datetime[0];
                    //this.dataForm.etimeStr = this.dataForm.datetime[1];
                    this.$request.post('/lsh/seller-manager/achieve/saveOrModify', this.dataForm, true, (res)=>{
                        if (res.ok) {
                            this.$message({type:'success', message:""});
                            this.business = 1;
                            this.list();
                        } else {
                            this.$message.error(res.msg);
                        }
                    });
                }
            });
        },
        // 
        cancelForm() {
            this.dataForm = {
                id: "",
                periodName: "",
                achievementType: 1,
                ruleHtml: "",
                datetime: [],
                stimeStr: "",
                etimeStr: "",
                templateId: "",
                topNum: "100",
                awards: []
            }
            this.business = 1;
            this.$refs['dataForm'].clearValidate();
        },
        // 
        periodAwardList() {
            this.$request.post('/lsh/seller-manager/achieve/queryAwardList', {periodId: this.rankForm.periodId}, true, (res)=>{
                if (res.ok) {
                    this.awardConfList = res.data || [];
                } else {
                    this.$message.error(res.msg);
                }
            });
        },
        // 
        listRank(_event, pageNo, pageSize) {
            let _pageNo = 1;
            if(pageNo) _pageNo = pageNo;
            this.rankForm.pageNo = _pageNo;
            let _pageSize = 10;
            if(pageSize) _pageSize = pageSize;
            this.rankForm.pageSize = _pageSize;

            this.loading = true;
            this.$request.post('/lsh/seller-manager/achieve/periodResultList', this.rankForm, true, (res)=>{
                this.loading = false;
                if (res.ok) {
                    this.tableRankList = res.data.list || [];
                    this.canExport = this.tableRankList.length==0?false:true;
                    this.initPagination(res.data.page||{});
                } else {
                    this.$message.error(res.msg);
                }
            });
        },
        resetRandForm(refreshListRank) {
            let periodId = this.rankForm.periodId
            this.rankForm = {
                pageNo: 1,
                pageSize: 10,
                periodId: periodId,
                rankSection: "",
                shopName: ""
            }
            if(refreshListRank===true) {
                this.listRank();
            }
        },
        // 
        exportDatas() {
            let url = "/lsh/seller-manager/achieve/exportPeriodResult";
            let xhr = new XMLHttpRequest();
            let formData = new FormData();
            for(let attr in this.rankForm) {
                formData.append(attr, this.rankForm[attr]);
            }
            xhr.overrideMimeType("text/plain; charset=x-user-defined");
            xhr.open('POST', url, true);
            xhr.responseType = "blob";
            xhr.responseType = "arraybuffer"
            xhr.setRequestHeader("token", sessionStorage.getItem('access_token'));
            xhr.setRequestHeader("loginId", sessionStorage.getItem('access_loginId'));
            xhr.onload = function(res) {
                if (this.status == 200) {
                    let blob = new Blob([this.response], {type: 'application/vnd.ms-excel'});
                    let respHeader = xhr.getResponseHeader("Content-Disposition");
                    let fileName = decodeURI(respHeader.match(/filename=(.*?)(;|$)/)[1]);
                    if (window.navigator.msSaveOrOpenBlob) {
                        navigator.msSaveBlob(blob, fileName);
                    } else {
                        let link = document.createElement('a');
                        link.href = window.URL.createObjectURL(blob);
                        link.download = fileName;
                        link.click();
                        window.URL.revokeObjectURL(link.href);
                    }
                }
            }
            xhr.send(formData);
        },
        // 
        judgePay() {
            this.$request.post('/lsh/seller-manager/achieve/judgePay', {periodId:this.rankForm.periodId}, true, (res)=>{
                if (res.ok) {// 
                    this.canPay = true;
                } else {// 
                    this.canPay = false;
                }
            });
        },
        // 
        sendRankPay() {
            if (!this.canPay) {
                this.$message.error('');
                return false;
            }
            this.$confirm('?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                this.canPay = false;
                this.$request.post('/lsh/seller-manager/achieve/payAchievement', {periodId: this.rankForm.periodId}, true, (res)=>{
                    if (res.ok) {
                        this.canPay = false;
                        this.listRank();
                        this.$message({type:'success', message:""});
                    } else {
                        this.canPay = true;
                        this.$message.error(res.msg);
                    }
                });
            }).catch(() => {});
        },
        openTplMsg() {
            this.tplMsgForm.show = true;
        },
        resetTplMsgForm() {
            this.tplMsgForm = {
                show: false,
                show: false,
                first: '',
                keyword1: '',
                keyword2: '',
                remark: '',
                templateUrl: '',
                sendScope: 2
            };
            this.$refs['tplMsgForm'].clearValidate();
        },
        confirmSendMsg(form) {
            if (this.sendMsg) {
                this.$message.error('');
                return false;
            }
            this.$refs[form].validate((valid) => {
                if (valid) {
                    this.sendMsg = true;
                    let params = {
                        wxTemplateParams: {
                            first: this.tplMsgForm.first,
                            keyword1: this.tplMsgForm.keyword1,
                            keyword2: this.tplMsgForm.keyword2,
                            remark: this.tplMsgForm.remark,
                            templateUrl: this.tplMsgForm.templateUrl
                        },
                        achieveParams: {
                            sendScope: this.tplMsgForm.sendScope,
                            periodId: this.rankForm.periodId
                        }
                    }
                    this.$request.post('/lsh/seller-manager/achieve/sendWechatTemplate', params, true, (res)=>{
                        this.sendMsg = false;
                        if (res.ok) {
                            this.tplMsgForm.show = false;
                            this.$message({type:'success', message: res.data});
                        } else {
                            this.$message.error(res.msg);
                        }
                    });
                }
            });
        }
    }
}
</script>

<style lang="scss" scoped>
    .space {position:relative;width:100%;height:20px;}
    .pl36 {padding-left:36px;}
    .form-note {font-size: 12px;color:#aaa;line-height:20px;}
    .el-input, .el-textarea {
        width: 600px;
    }
    .el-date-editor {
        width: 288px;
    }
    .search-block {
        .el-input, .el-select {
            width: 200px;
        }
    }
    .tplM-msg-bloc {
        width: 100%;
        .el-input, .el-textarea {
            width: 80%;
        }
    }
    .award-table {
        margin-top: 10px;
        width: 600px;
        text-align: center;
        border-color:#ebeef5;
        border-collapse: collapse;
        color: #707478;
        thead {
            background: #ebeef5;
        }
        td {
            width: 33%;
            input {
                color: #888;
                outline:none;
                text-align: center;
                width: 90%;
                height: 24px;
                padding: 0 5px;
                border: none;
                background:transparent;
            }
            input::focus {
                outline:none;
                background-color: transparent;
            }
            input::selection{background:transparent;}
            input::-moz-selection{background:transparent;}
            input.one-cell-two {
                width: 35%;
            }
        }
    }
</style>



