<template>
  <div class="amount-examine-container">
    <div class="box-container">
      
      <!---->
      <el-col :span="24" class="toolbar" style="padding-bottom: 0px;margin-bottom: 0">
        <el-form :inline="true" :model="filters" label-width="80px">
          <el-form-item :size="'small'" label="">
            <el-select
                v-model="filters.status"
                placeholder=""
                style="width: 200px">
              <el-option
                  v-for="item in statusList"
                  :key="item.code"
                  :label="item.name"
                  :value="item.code">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item :size="'small'" label="">
            <el-input placeholder="" v-model="filters.keywords" class="input-with-select" style="width: 300px">
              <el-select v-model="filters.searchType" slot="prepend" @change="inputWithSelectChange" placeholder="" style="width: 120px">
                <el-option label="" value="1"></el-option>
                <el-option label="" value="2"></el-option>
                <el-option label="" value="4"></el-option>
              </el-select>
            </el-input>
          </el-form-item>
          <el-form-item :size="'small'" label="">
            <el-date-picker
                v-model="filters.time"
                type="datetimerange"
                placeholder=""
                value-format="yyyy-MM-dd HH:mm"
                range-separator=""
                start-placeholder=""
                end-placeholder=""
                style="width: 350px">
            </el-date-picker>
          </el-form-item>

          <div></div>
          <el-form-item size="small" label="" prop="addrProvince">
            <el-select
                v-model="filters.addrProvince"
                placeholder=""
                @change="selectBrand1"
                style="width: 160px">
              <el-option
                  v-for="item in cateLvl1List"
                  :key="item.code"
                  :label="item.name"
                  :value="item.code">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item size="small" label="" prop="addrCity">
            <el-select
                v-model="filters.addrCity"
                placeholder=""
                @change="selectBrand2"
                style="width: 160px">
              <el-option
                  v-for="item in cateLvl2List"
                  :key="item.code"
                  :label="item.name"
                  :value="item.code">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item size="small" label="" prop="addrArea">
            <el-select
                v-model="filters.addrArea "
                placeholder=""
                style="width: 160px">
              <el-option
                  v-for="item in cateLvl3List"
                  :key="item.code"
                  :label="item.name"
                  :value="item.code">
              </el-option>
            </el-select>
          </el-form-item>
          <div></div>
          <el-form-item class="mr0" :size="'small'">
            <el-button type="primary" size="small" @click="commitForm"></el-button>
            <el-button size="small" class="important" @click="getStatus"></el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </div>

    <div class="box-container">
      <!---->
      <el-col class="crm-table-wrap" v-loading="listLoading" :span="24">
        <el-table
            :data="TxList"
            style="width: 100%">
          <el-table-column
              type="index"
              label=""
              width="50">
          </el-table-column>
          <el-table-column
              prop="sellerInfo.shopName"
              label="	"
              width="200">
          </el-table-column>
          <el-table-column
              prop="sellerInfo.ownerName"
              label=""
              width="120">
          </el-table-column>
          <el-table-column
              prop="sellerInfo.phoneNo"
              label=""
              width="120">
          </el-table-column>
          <el-table-column
              prop="balance"
              label=""
              width="120">
          </el-table-column>
          <el-table-column
              prop="txAmount"
              label=""
              width="120">
            <template slot-scope="scope">
              <span style="color: red">{{ scope.row.txAmount }}</span>
            </template>
          </el-table-column>

          <el-table-column
              prop="txTime"
              label=""
              width="200">
            <template slot-scope="scope">
              <span>{{ new Date(scope.row.txTime).Format('yyyy-MM-dd hh:mm:ss')}}</span>
            </template>
          </el-table-column>
          <el-table-column
              prop="txTime"
              label=""
              width="200">
            <template slot-scope="scope">
              <!--<span>{{ new Date(scope.row.txTime).Format('yyyy-MM-dd hh:mm:ss')}}</span>-->
            </template>
          </el-table-column>
          <el-table-column
              prop="authStatus"
              label="">
            <template slot-scope="scope">
              <span v-if="scope.row.status == '1'">{{ "" }}</span>
              <span v-else-if="scope.row.status == '2'">{{ "" }}</span>
              <span v-else-if="scope.row.status == '3'">{{ "" }}</span>
            </template>
          </el-table-column>
          <el-table-column
              fixed="right"
              label=""
              width="240"
          >
            <template slot-scope="scope" >
              <el-button
                  type="text"
                  size="mini"
                  @click="sellerDetail(scope.row.sellerId)"
              ></el-button>
              <el-button
                  :disabled="scope.row.status !== 1"
                  type="text"
                  size="mini"
                  @click="disapprovalDialog(scope.row.id)"
              ></el-button>
              <el-button
                  :disabled="scope.row.status !== 1"
                  type="text"
                  size="mini"
                  @click="approval(scope.row.id)"
              ></el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-col>

      <!---->
      <el-col :span="24" class="footer-toolbar">

        <!--<el-button type="primary" class="btn-jump-to" @click="jumpTo"></el-button>-->
        <el-pagination
            background
            layout="total,prev, pager, next,jumper"
            :current-page="currentPage"
            @current-change="handleCurrentChange"
            :page-size="pageSize"
            :total="total"
            style="float:right;">
        </el-pagination>
      </el-col>
    </div>
    <el-dialog
        title=""
        :visible.sync="dialogVisible"
        width="30%"
        :before-close="cancelDialog">
      <el-input
          type="textarea"
          :rows="2"
          placeholder=""
          v-model="failReason">
      </el-input>
      <div style="text-align: center;margin-top: 30px">
        <el-button size="small" @click="cancelDialog"> </el-button>
        <el-button size="small" type="primary" @click="disapproval"> </el-button>
      </div>

    </el-dialog>


  </div>
</template>

<script>
  export default {
    name: "Reviewmanage",
    data(){
      return{
        listLoading:false,

        //
        statusList:[
          {
            code:"",
            name:""
          },
          {
            code:"1",
            name:""
          },
          {
            code:"2",
            name:""
          },
          {
            code:"3",
            name:""
          },
        ],
        //
        cateLvl1List:[],
        cateLvl2List:[],
        cateLvl3List:[],

        filters: {
          //1-2-3-
          status :'1',

          //  1-2-3-4-5-
          searchType:'',
          //
          keywords:'',
          //
          // appStartTime:'',
          // //
          // appEndTime:'',
          time:[],

          //
          addrProvince:'',
          //
          addrCity:'',
          //
          addrArea:'',



        },
        //
        TxList:[],

        //
        pageSize:10,

        pageNo: 1,
        //
        currentPage:1,
        //
        total:0,


        //
        dialogVisible:false,
        failReason:'',
        txId:''
      }

    },
    created(){
      this.getListTx()
      this.getOneCategory()
    },
    methods:{
      //1
      getOneCategory(){
        this.$request.post('/lsh/seller-manager/region/province',{}, true, (res) => {
          this.cateLvl1List = [...res]
        })
      },
      selectBrand1(){
        this.filters.addrCity = ''
        this.cateLvl2List = []
        this.filters.addrArea = ''
        this.cateLvl3List = []
        this.getTwoCategory()
      },
      //2
      getTwoCategory(){
        if(this.filters.addrProvince  == '')return
        this.$request.post('/lsh/seller-manager/region/newCity',{parentCode:this.filters.addrProvince}, true, (res) => {
          this.cateLvl2List = [...res]
        })
      },
      selectBrand2(){
        this.filters.addrArea = ''
        this.cateLvl3List = []
        this.getThreeCategory()
      },
      //3
      getThreeCategory(){
        if(this.filters.addrCity == '')return
        this.$request.post('/lsh/seller-manager/region/newDistrict',{parentCode:this.filters.addrCity}, true, (res) => {
          this.cateLvl3List = [...res]
        })
      },
      //
      getListTx() {
        let params = {
          //
          status:this.filters.status,


          searchType:this.filters.searchType,
          //
          keywords:this.filters.keywords,

          //
          addrProvince: this.filters.addrProvince,
          addrCity: this.filters.addrCity,
          addrArea: this.filters.addrArea,
          //
          stime: this.filters.time?this.filters.time[0]?this.filters.time[0]:'':'',
          etime: this.filters.time?this.filters.time[1]?this.filters.time[1]:'':'',


          pageNo: this.pageNo,
          pageSize: 10,

        };

        this.postSearch(params)
      },
      postSearch(params) {
        // this.listLoading = true;
        this.$request.post('/lsh/seller-manager/seller/txList', params, false, (res) => {

            console.log(res.data)
            // this.listLoading = false;
            this.TxList = res.data.list
            this.total = res.data.page.count
            this.pageNo = res.data.page.pageNo

        })
      },
      //
      commitForm() {
        this.pageNo = 1
        this.currentPage = 1
        this.getListTx()
      },
      //
      getStatus() {
        this.filters.status = '1'

        this.filters.searchType = ''
        //
        this.filters.keywords = ''
        this.filters.time = []

        //
        this.filters.addrProvince = ''
        //
        this.filters.addrCity = ''
        //
        this.filters.addrArea = ''

        this.pageNo = 1
        this.currentPage = 1
        this.getListTx()
      },
      inputWithSelectChange(val){
        this.filters.keywords = ""
      },

      //
      approval(txId){
        let params = {txId,authResult:1}
        this.$confirm(``, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$request.post('/lsh/seller-manager/seller/txAuth',params,false,res => {
            if(res.ok){
              this.$message({
                message: '',
                type: 'success'
              });
              this.getListTx()
            }else{
              console.log(res)
              this.$message({
                message: res.msg,
                type: 'warning'
              });
            }
          })
        })
      },
      //
      disapprovalDialog(txId){
        this.dialogVisible = true
        this.txId = txId
      },
      disapproval(){
        let params = {txId:this.txId,authResult:0,authDesc:this.failReason}
        this.$confirm(``, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$request.post('/lsh/seller-manager/seller/txAuth',params,false,res => {
            if(res.ok){
              this.$message({
                message: '',
                type: 'success'
              });
              this.dialogVisible = false
              this.failReason = ""
              this.getListTx()
            }else{
              console.log(res)
              this.$message({
                message: res.msg,
                type: 'warning'
              });
              this.dialogVisible = false
              this.failReason = ""
              this.getListTx()
            }
          })
        })
      },
      cancelDialog(){
        this.dialogVisible = false
        this.failReason = ""
      },
      sellerDetail(sellerId){
        this.$router.push({
          path:'/seller/tx/sellerDetail?sellerId='+sellerId
        })
      },
      //
      jumpTo() {
        this.$emit('current-change')
      },
      //
      handleCurrentChange(val) {
        this.pageNo = val
        this.currentPage = val
        this.getListTx()
      },
    }
  }
</script>

<style lang="scss" scoped>
  .amount-examine-container{
  .box-container{
    background-color: #fff;
    padding: 15px;
    margin-bottom: 30px;
    add-new-seller{
      margin-bottom: 30px;
    }
  &:after{
     content: '';
     display: block;
     width: 0;
     height: 0;
     clear: both;
   }
  }
  }
  .el-form-item {
    margin-bottom: 20px;
  }
</style>
<style >
  .amount-examine-container .el-table{text-align: center}
  .amount-examine-container .el-table th{text-align: center}
  .amount-examine-container .el-table td{text-align: center}
  .amount-examine-container .mini .el-form-item__label {width: 40px!important;}
</style>


